<i18n>
{
  "en": {
    "Feedback": "Feedback",
    "SubmittedSuccessfully": "Submitted successfully",
    "describe": "Thank you for your feedback. We will handle your feedback seriously and reply to you as soon as possible. Thank you!",
    "Complete": "Complete"
  },
  "zh": {
    "Feedback": "意见反馈",
    "SubmittedSuccessfully": "提交成功",
    "describe": "感谢你的反馈，我们会认真处理你的反馈，尽快回复你，谢谢！",
    "Complete": "完成"
  }
}
</i18n>

<template>
  <div class="feedbackSuccess">
    <head-top :title='$t("Feedback")' :custom-back="true" @goBackHander="goHandle"></head-top>
    <div class="container">
      <img src="../../assets/feedback_images/succeed.png" alt="">
      <strong>{{ $t("SubmittedSuccessfully") }}</strong>
      <span>{{ $t("describe") }}</span>
      <button @click="goHandle" class="btn">{{ $t("Complete") }}</button>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import { routerGo } from '../../utils/router.js'
export default {
  name: 'feedbackSuccess',
  data () {
    return {}
  },

  components: {
    headTop
  },

  methods: {
    ...mapActions([
      'sidebarSwitch'
    ]),

    goHandle () {
      this.sidebarSwitch(false)
      routerGo(-2)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.feedbackSuccess{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    padding-top: 115px;
    img{
      height: 180px;
      display: block;
      margin: 0 auto 35px auto;
    }
    strong{
      font-size: 36px;
      display: block;
    }
    span{
      .wh(520px, 100px);
      display: block;
      font-size: 30px;
      margin: 26px auto 0 auto;
    }
    button{
      .wh(690px, 100px);
      background: @themeColor;
      border-radius: 10px;
      font-size: 30px;
      position: absolute;
      left: 50%;
      bottom: 50px;
      margin-left: -345px;
      box-shadow: 0px 4px 25px@themeColor;
      color: #5A5A57;
    }
  }
}
</style>



